import { useSnackbar } from 'notistack';
export var useErrorNotification = function () {
    var enqueueSnackbar = useSnackbar().enqueueSnackbar;
    var errorNotification = function (message) {
        enqueueSnackbar(message ? message : 'Помилка виконання запиту', {
            variant: 'error',
            anchorOrigin: {
                vertical: 'top',
                horizontal: 'right',
            },
        });
    };
    return { errorNotification: errorNotification };
};
